/* 控件 */

div.vslt {
  position: relative;
  width: 90%;
  min-width: 170px;
  height: 30px;
  border: 1px solid #dadada;
  background: #fff;
  border-radius: 3px;

  select {
    position: relative;
    width: 100%;
    height: 30px;
    text-indent: 5px;
    font-size: 14px;
    background: none;
    border: none;
    -webkit-appearance: none;
    outline: none;
    z-index: 2;
  }

  span {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-left: 1px solid #dadada;
    background: #f6f6f6;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    z-index: 1;

    i {
      font-size: 14px;
      color: #888;
    }
  }
}

input.vinput {
  width: 90%;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  text-indent: 5px;
  border: 1px solid #dadada;
  border-top-color: #c5c5c5;
  border-left-color: #c5c5c5;
  border-radius: 3px;
}

textarea.vtextarea {
  width: 90%;
  height: 40px;
  font-size: 14px;
  border: 1px solid #dadada;
  border-top-color: #c5c5c5;
  border-left-color: #c5c5c5;
  border-radius: 3px;
  -webkit-appearance: none;
}

input.vswitch-1 {
  display: none;

  + label {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 15px;
    background-color: #fafbfa;
    padding: 9px;
    border-radius: 50px;
    -webkit-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      -webkit-transition: box-shadow 0.1s ease-in;
      transition: box-shadow 0.1s ease-in;
      border-radius: 100px;
      box-shadow: inset 0 0 0 0 #eee, 0 0 1px rgba(0, 0, 0, 0.4);
    }

    &:before {
      content: '';
      position: absolute;
      left: 1px;
      top: 1px;
      width: 31px;
      height: 31px;
      background: #fff;
      z-index: 99;
      -webkit-transition: all 0.1s ease-in;
      transition: all 0.1s ease-in;
      border-radius: 100px;
      box-shadow: 0 3px 1px rgba(0, 0, 0, 0.05), 0 0px 1px rgba(0, 0, 0, 0.3);
    }
  }

  &:active + label {
    &:after {
      box-shadow: inset 0 0 0 20px #eee, 0 0 1px #eee;
    }

    &:before {
      width: 37px;
    }
  }

  &:checked {
    &:active + label:before {
      width: 37px;
      left: 20px;
    }

    + label {
      &:before {
        content: '';
        position: absolute;
        left: 26px;
        border-radius: 100px;
      }

      &:after {
        content: '';
        font-size: 1.5em;
        position: absolute;
        background: #5dc800;
        box-shadow: 0 0 1px #5dc800;
      }
    }
  }

  + label:active {
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px 1px 3px rgba(0, 0, 0, 0.1);
  }
}

i {
  &.vinclrbtn {
    display: none;
    position: absolute;
    right: 6px;
    font-size: 14px;
    color: #aaa;
    z-index: 99;
  }

  &.vineyebtn {
    display: inline;
    position: absolute;
    right: 6px;
    font-size: 18px;
    color: #aaa;
    z-index: 99;

    &.visible {
      color: #09F;
    }
  }
}

input.variseclear {
  &::-ms-clear {
    display: none;
  }

  &:valid + i.vinclrbtn {
    display: inline;
  }
}

/* 字符截取省略 */

.strcut {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.mustrcut {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;

  &.l3 {
    -webkit-line-clamp: 3;
  }
}

