.header {
  padding: 5px 0;
  min-width: 320px;
  height: 30px;
  line-height: 30px;
  background: #177cef;
  position: relative;

  h1 {
    font-size: 18px;
    color: #ffffbb;
    text-align: center;
    font-weight: bold;
  }

  h2 {
    font-size: 16px;
    color: #ddeeff;
    text-align: center;
  }

  div {
    &.op {
      position: absolute;
      top: 0;
      line-height: 40px;

      &.lt {
        left: 0;
      }

      &.rt {
        right: 0;
      }
    }

    &.lt a {
      display: inline-block;
      margin-left: 5px;
      min-width: 30px;
    }

    &.rt a {
      margin-right: 5px;
    }

    &.op i {
      color: #fff;
    }
  }
}

