/* 弹出对话框 */

.vds-dialog {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;

  .mask {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
  }

  .wrap {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .layer {
    min-width: 180px;
    max-width: 300px;
    height: auto;
    background: #fff;
    border-radius: 5px;
    overflow: hidden;

    .con {
      padding: 20px;
      text-align: center;

      p {
        display: inline-block;
        text-align: justify;
        font-size: 14px;
        color: #555;
      }
    }

    .bom {
      text-align: center;
      border-top: 1px solid #e6e6e6;
      background: #f7f7f7;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      overflow: hidden;

      a {
        display: block;
        height: 42px;
        line-height: 42px;
        font-size: 14px;
      }

      .cancel {
        float: left;
        width: 50%;
        color: #669999;
        box-sizing: border-box;
        border-right: 1px solid #e7e7e7;
      }

      .ok {
        float: right;
        width: 50%;
        color: #0a7bff;
        box-sizing: border-box;
        border-left: 1px solid #fff;
      }

      .close {
        color: #0a7bff;
        font-size: 14px;
      }
    }
  }
}

