/* loading */

div.loadbar {
  width: 100%;
  height: auto;
  padding: 5px 0;
  overflow: hidden;
  z-index: 99999;

  p {
    text-align: center;
    color: #b5b5b5;
    height: 20px;
    line-height: 20px;
  }
}

i.rec-loading {
  display: block;
  margin: 0 auto;
  width: 100px;
  height: 9px;
  line-height: 0;
  font-size: 0;
  background: url(../images/rec-loading.gif) no-repeat center;
}

.loading-pop {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;

  .mask {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
  }

  .wrap {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
  }

  i {
    &:before, &:after {
      display: block;
      width: 2.2em;
      height: 2.2em;
      border-radius: 50%;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
      -webkit-animation: loading-pop 1.8s infinite ease-in-out;
      animation: loading-pop 1.8s infinite ease-in-out;
    }

    display: block;
    width: 2.2em;
    height: 2.2em;
    border-radius: 50%;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation: loading-pop 1.8s infinite ease-in-out;
    animation: loading-pop 1.8s infinite ease-in-out;
    position: relative;
    font-size: 10px;
    text-indent: -9999em;
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;

    &:before {
      left: -3.5em;
      -webkit-animation-delay: -0.32s;
      animation-delay: -0.32s;
    }

    &:after {
      left: 3.5em;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
    }
  }
}

i:after {
  content: '';
  position: absolute;
  top: 0;
}

@-webkit-keyframes loading-pop {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em #333;
  }

  40% {
    box-shadow: 0 2.5em 0 0 #f2f2f2;
  }
}

@keyframes loading-pop {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em #333;
  }

  40% {
    box-shadow: 0 2.5em 0 0 #f2f2f2;
  }
}
